body {
  height: 100vh;
  color: #fff;
  background-color: #222;
  justify-content: center;
  align-items: center;
  display: flex;
}

.calculator {
  width: 250px;
  background-color: #222;
  border-radius: 10px;
  flex-direction: column;
  display: flex;
  box-shadow: 0 0 10px rgba(0, 0, 0, .4);
}

#display {
  height: 40px;
  color: #fff;
  background-color: #ec042d;
  border: none;
  border-bottom: 1px solid #555;
  outline: none;
  padding: 0 10px;
  font-size: 20px;
}

.buttons {
  grid-template-columns: repeat(4, 1fr);
  gap: 1px;
  display: grid;
}

button {
  cursor: pointer;
  background-color: #333;
  border: none;
  padding: 10px;
  font-size: 18px;
  transition: background-color .3s;
}

.buttons button {
  color: #fff;
}

button:hover {
  background-color: #ec042d;
}

button:active {
  background-color: #50ff32;
}

button.operator {
  color: #fff;
  background-color: #f0f;
}

button.operator:hover {
  background-color: #f3f;
}

button.operator:active {
  background-color: #f6f;
}

button.equal {
  color: #222;
  background-color: #0fc;
}

button.equal:hover {
  background-color: #33ffd4;
}

button.equal:active {
  background-color: #6fd;
}

body {
  min-height: 100vh;
  flex-direction: column;
  display: flex;
}

main {
  flex: 1;
}

footer {
  text-align: center;
  color: #ff3842;
  padding: 10px 0;
}

